.sc-banner {
  padding-top: 50px;
  padding-bottom: 38px;
  position: relative;
}
.sc-banner .primary-btn {
  display: inline-block;
  position: relative;
  background-color: #5F1FEC;
  border-radius: 50px;
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
  color: #FFFFFF;
  text-decoration: none;
  padding: 14px 58px 14px 28px;
}
.sc-banner .primary-btn span {
  color: #FFFFFF;
  transition: 0.3s;
  position: relative;
  z-index: 1;
}
.sc-banner .primary-btn .icon {
  width: 41px;
  height: calc(100% - 6px);
  border-radius: 50px;
  background-color: #fff;
  display: inline-block;
  position: absolute;
  right: 3px;
  top: 0;
  bottom: 0;
  margin: auto;
  transition: 0.3s;
}
.sc-banner .primary-btn .icon:before {
  left: auto;
  right: 12px;
}
.sc-banner .primary-btn:hover span {
  color: #5F1FEC;
}
.sc-banner .primary-btn:hover .icon {
  width: calc(100% - 6px) !important;
}
.sc-banner .container {
  position: relative;
}
.sc-banner .bubble__img {
  position: absolute;
  z-index: -1;
  right: 124px;
  top: -180px;
  max-width: 840px;
}
.sc-banner .bubble__img img {
  max-width: 100%;
}
.sc-banner .head__info p {
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
  color: #4D4D4D;
  margin-bottom: 33px;
}
.sc-banner .banner__details {
  width: 654px;
  display: flex;
  margin-left: auto;
  margin-top: 120px;
  justify-content: space-between;
  margin-bottom: -95px;
  z-index: 1;
  position: relative;
}
.sc-banner .detail__box {
  background-color: #98FF7A;
  border-radius: 16px;
  padding: 38px 12px 18px 12px;
  align-self: center;
  width: 179px;
  position: relative;
  margin-top: 79px;
}
.sc-banner .detail__box .icon {
  top: 5px;
  right: 5px;
  width: 44px;
  height: 44px;
  border-radius: 100%;
  position: absolute;
  background-color: #D8FFC3;
  display: inline-block;
}
.sc-banner .detail__box p {
  font-weight: 600;
  font-size: 24px;
  line-height: 18px;
  color: #000000;
  margin-bottom: 0;
  padding-bottom: 19px;
  border-bottom: solid 1px #fff;
  margin-bottom: 10px;
}
.sc-banner .detail__box p span {
  font-size: 13.6px;
  line-height: 16px;
  display: inline-block;
  width: 100%;
}
.sc-banner .detail__box .description {
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  color: #000000;
}
.sc-banner .phone__box {
  position: relative;
  width: 232px;
  flex-shrink: 0;
  margin-left: 30px;
  margin-right: 30px;
}
.sc-banner .phone__box .phone-frame {
  width: 100%;
}
.sc-banner .phone__text {
  padding-left: 20px;
  padding-right: 20px;
  position: absolute;
  top: 55px;
  width: 100%;
  text-align: center;
}
.sc-banner .phone__text .python__logo {
  width: 74px;
  margin: auto;
}
.sc-banner .phone__text .python__logo img {
  max-width: 100%;
}
.sc-banner .phone__text h4 {
  margin-top: 15px;
  font-weight: 500;
  font-size: 21.7px;
  line-height: 26px;
  color: #805DD4;
  margin-bottom: 12px;
}
.sc-banner .phone__text p {
  font-weight: 600;
  font-size: 9.9px;
  line-height: 12px;
  color: #666666;
  margin-bottom: 6px;
}
.sc-banner .phone__text .price__box {
  display: inline-block;
  width: 100%;
  border: solid 2px #98FF7A;
  border-radius: 12px;
  margin-bottom: 26px;
  margin-top: 10px;
  padding: 9px 10px 14px 10px;
  text-align: center;
}
.sc-banner .phone__text .price__box .price {
  font-weight: 600;
  font-size: 24px;
  line-height: 29px;
  color: #000000;
  margin-bottom: 4px;
}
.sc-banner .phone__text .price__box .installments {
  display: inline-block;
  width: 100%;
  font-weight: 600;
  font-size: 11px;
  line-height: 13px;
  color: #848484;
}
.sc-banner .phone__text .primary-btn {
  font-size: 12px;
  padding-left: 20px;
  width: 100%;
}

@media (max-width: 1280px) {
  .sc-banner h1 {
    font-size: 46px;
  }
  .sc-banner .head__info {
    max-width: 351px;
  }
  .sc-banner .banner__details {
    margin-left: -205px;
    margin-top: 55px;
  }
  .sc-banner .bubble__img {
    right: 0px;
    top: -54px;
    max-width: 588px;
  }
}
@media (max-width: 991px) {
  .sc-banner .bubble__img {
    top: 0px;
    max-width: 390px;
  }
  .sc-banner .banner__details {
    width: 100%;
    margin-top: 30px;
    margin-right: auto;
    margin-left: auto;
  }
}
@media (max-width: 767px) {
  .sc-banner {
    overflow: hidden;
  }
  .sc-banner h1 {
    font-size: calc(4vw + 24px);
  }
  .sc-banner .primary-btn {
    width: 100%;
  }
  .sc-banner .bubble__img {
    top: -53px;
    max-width: 218px;
    right: 5px;
    opacity: 0.5;
  }
  .sc-banner .banner__details {
    flex-wrap: wrap;
    margin-top: 30px;
    margin-bottom: 0;
  }
  .sc-banner .detail__box {
    align-self: auto;
    margin-top: 0;
    width: calc(50% - 5px);
  }
  .sc-banner .detail__box .icon {
    width: 39px;
    height: 39px;
  }
}